canvas {
   width: 100%;
   height: 100%;
}

.panel {
   width: 50vw;
   height: 50vw;
   max-width: 80vh;
   max-height: 80vh;
}

.flex {
   display: flex;

   &-row {
      flex-direction: row;
   }

   &-column {
      flex-direction: column;
      padding-top: 10px;
   }
}

input {
   margin-right: 20px;
}

form {
   margin-top: 10px;
}